
import React, { useContext, useEffect, useState } from 'react'
import { Modal } from 'react-bootstrap';
import { Button } from 'react-bootstrap';
import { getNotes } from './api/getNotes';
import { insertNote } from './api/insertNote';
import { IATFAuditContext } from './context/IATFAuditContext';
import { formatDateTime } from './helpers/formatDateTime';
import { getCurrentDateTime } from './helpers/getCurrentDateTime';
import { useForm } from './hooks/useForm';


export const AddNotes = ({show, onHide, setNote}) => {

    const auditContext = useContext(IATFAuditContext);

    const auditID = auditContext.data.id;
    
    const [formState, handleInputChange, , reset] = useForm({
        id: 0,
        auditid: auditID,
        notes: "",
        creationDate: getCurrentDateTime(),
    });

    const {notes} = formState;

    const [newNote, setNewNote] = useState({
        id: 0,
    });

    const save = () => {

        const resp = insertNote( formState );
        resp.then(saved => {
            
            console.log('Success:'+saved.id);
            
            setNote({
                id: saved.id,
                auditid: saved.auditid,
                notes: saved.notes,
                creationDate: saved.creationDate,
            })
            reset();

            setNewNote({
                id: saved.id,
            })
        })

    }

    const [noteList, setNoteList ] = useState();

    useEffect(() => {

        getNotes( auditID )
        .then( response => {
            setNoteList(response);
        });

    }, [newNote, auditID]);

    
    return (
        <Modal 
            size="lg"
            aria-labelledby="contained-modal-title-vcenter"
            centered
            show={show} 
            backdrop="static"
            keyboard={false}
        >
            <Modal.Header>
                <Modal.Title>Audit Notes</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <div className="notes-table-container">
                    <table className="table primary">
                        <tbody>
                            {
                                noteList && noteList.data.map( note => (
                                    <tr key={note.id}>
                                        <td key={note.id}>
                                            <p className="note-time">{formatDateTime(note.creationDate)}:</p>
                                            <br/>
                                            <p className="note-description">{note.notes}</p>
                                        </td>
                                    </tr>
                                ))
                            }
                        </tbody>
                    </table>
                </div>
                <input 
                    type="text"
                    size="86"
                    name="notes" 
                    value={notes}
                    placeholder="Enter new note here"
                    onChange={handleInputChange}
                />
                &nbsp;&nbsp;
                <Button 
                    className="btn-blue" 
                    onClick={save}>
                    SAVE
                </Button>
            </Modal.Body>
            <Modal.Footer>
                <Button 
                    className="btn-blue" 
                    onClick={onHide}>
                    CLOSE
                </Button>                
            </Modal.Footer>
        </Modal>
    )
}
