
import configData from "../config/config.json";

export const getZippedFileNames = async( IATFauditID, fileType ) => {

    const url = `${configData.server_url}/api/report/zip/${IATFauditID},${fileType}`;

    const resp = await fetch(url);

    const data = await resp.json();  

    const reports = data.map( report => {

        return {
            id: report.id,
            auditID: report.auditID,
            type: report.type, 
            name: report.name
        }

    })

    return(reports);
}
