
import ReportType from '../globals/ReportTypes.js';


export const getDropZoneMessage = (fileType) => {
    let message = '';

    switch (fileType) {
        case ReportType.MW_ZIP_FILE:
            message = "Preliminary Report ZIP File";
            break;
        case ReportType.V1_ZIP_FILE:
            message = "Audit ZIP File";
            break;
        case ReportType.V2_ZIP_FILE:
            message = "Final Audit Report ZIP File";
            break;
        case ReportType.APPROVAL_EMAIL:
            message = "Approval e-mail File";
            break;

        default:
            break;
    }

    return (message);
}
