
import React, { useContext, useState } from 'react'
import { IATFAuditContext } from './context/IATFAuditContext'
import { updateIATFAudit } from './api/updateIATFAudit'
import DatePicker from "react-datepicker";
import { formatDate } from './helpers/formatDate';
import IATFAuditUpdateTypes from './globals/IATFAuditUpdateTypes.js';
import { setStartDate } from './helpers/setStartDate';
import "react-datepicker/dist/react-datepicker.css";


export const VetoRejection = () => {

    const IATFAudit = useContext(IATFAuditContext);

    const auditID = IATFAudit.data.id;

    const [formState, setFormState] = useState({
        id: auditID,
        vetorejected: IATFAudit.data.vetorejected,
        vetorejecteddate: IATFAudit.data.vetorejecteddate ? formatDate(IATFAudit.data.vetorejecteddate) : null,
        vetofeedback: IATFAudit.data.vetofeedback,
    });

    const { vetorejected, vetorejecteddate, vetofeedback } = formState;

    const startDate = setStartDate(vetorejecteddate);

    const handleInputTextChange = (e) => {

        setFormState({
            ...formState,
            [e.target.name]:e.target.value
        })

    }    

    const handleInputChange = (e) => {

        setFormState({
            ...formState,
            [e.target.name]:e.target.checked
        })

    }

    const handleDateChange = (date) => {

        const newDate = date ? formatDate(date) : null;

        setFormState({
            ...formState,
            vetorejecteddate:newDate
        })
    }

    const save = () => {
        const resp = updateIATFAudit( formState, IATFAuditUpdateTypes.REJECTION );

        resp.then(saved => {
            console.log('Success:', saved);
            // if ( saved ){
            //     var element = document.getElementById("save");
            //     element.classList.add("btn-green");
            //     element.innerHTML = 'Saved !';
            // }
        })
    }


    return (
        <div className="rejection">
            <h5>Veto Rejection</h5>
            <br/>
            <div className="rejection-container">
                <table width="100%">
                    <tbody>
                        <tr>
                            <td>
                            <div className="form-switch">
                                <input 
                                    type="checkbox" 
                                    className="form-check-input"
                                    id="vetorejected"
                                    name="vetorejected"
                                    checked={vetorejected} 
                                    onChange={handleInputChange}
                                />
                                <label htmlFor="vetorejected">&nbsp;&nbsp;Documentation rejected by Veto on&nbsp;&nbsp;</label>
                                </div>
                            </td>
                            <td>
                                <DatePicker 
                                    className="datepicker"
                                    id="vetorejecteddate"
                                    name="vetorejecteddate"
                                    value={vetorejecteddate} 
                                    selected={startDate} 
                                    onChange={(date) => handleDateChange(date)} 
                                />
                            </td>
                        </tr>
                    </tbody>
                </table>
                <br/>
                <label htmlFor="vetofeedback">Veto Feedback</label>
                <div>
                    <textarea 
                        className="form-control" 
                        id="vetofeedback"
                        name="vetofeedback"
                        rows="3"
                        value={vetofeedback}
                        onChange={handleInputTextChange}
                    >
                    </textarea>
                </div>
            </div>
            <div className="btn-wrapper">
                <button 
                    id="save" 
                    className="btn-blue" 
                    onClick={save}
                >
                    SAVE
                </button>
            </div>
        </div>
    )
}
