import React from 'react'
import ReportTypes from './globals/ReportTypes'

export const ShowV2FileOptions = ({file, handleOnChange}) => {
    return (
        <select 
            id={file.id}
            name={file.id}                                            
            key={file.id}
            className="form-select"
            value={file.type} 
            onChange={handleOnChange} >
                <option value={ReportTypes.UNKNOWN}>Unknown</option>
                <option value={ReportTypes.V2_CARA_REPORT_FINAL}>CARA Report - final</option>
                <option value={ReportTypes.V2_CARA_NC_REPORT_FINAL}>CARA NC-Report -final</option>
                <option value={ReportTypes.V2_NCM_EVIDENCES}>NCM Evidences</option>
                <option value={ReportTypes.V2_RELEASE_PROTOCOL}>VETO-2 Release Protocol</option>  
                <option value={ReportTypes.V2_AUDIT_PLAN}>Audit Plan and Release Protocol in attachment to CARA print/file </option>  
                <option value={ReportTypes.V2_CARA_SPA}>CARA SPA Report</option>
                <option value={ReportTypes.V2_DE_CERT}>De-Cert-Workflow</option>  
                <option value={ReportTypes.V2_OTHER_DOCUMENTS}>Other Documents/files/records</option>  
        </select>
    )
}

