
import React, { useContext, useState } from 'react'
import { updateIATFAudit } from './api/updateIATFAudit';
import IATFAuditUpdateTypes from './globals/IATFAuditUpdateTypes'
import VetoPersonTypes from './globals/VetoPersonTypes';
import { IATFAuditContext } from './context/IATFAuditContext'
import DatePicker from "react-datepicker";
import { formatDate } from './helpers/formatDate';
import "react-datepicker/dist/react-datepicker.css";
import { setStartDate } from './helpers/setStartDate';


const ProjectManagerReviewV1 = () => {

    const IATFAudit = useContext(IATFAuditContext);

    const [formState, setFormState] = useState({
        id: IATFAudit.data.id,
        veto1docsubmitted: IATFAudit.data.veto1docsubmitted || false,
        veto1docsubmitdate: IATFAudit.data.veto1docsubmitdate ? formatDate(IATFAudit.data.veto1docsubmitdate) : null,
        vetopersonname: IATFAudit.data.vetopersonname,
        vetopersonemail: IATFAudit.data.vetopersonemail,
        vetopersontype: IATFAudit.data.vetopersontype
    });

    const {veto1docsubmitted, veto1docsubmitdate, vetopersonname, vetopersonemail, vetopersontype} = formState;

    const startDate = setStartDate(veto1docsubmitdate);

    // useEffect( () => {
        
    //     getIATFAudit(auditID)
    //         .then( audit => {
    //             setFormState({
    //                 id: audit.id,
    //                 veto1docsubmitted: audit.veto1docsubmitted,
    //                 veto1docsubmitdate: audit.veto1docsubmitdate,
    //                 vetopersonname: audit.vetopersonname,
    //                 vetopersonemail: audit.vetopersonemail,
    //                 vetopersontype: audit.vetopersontype
    //             })

    //         });
    //         // .catch( console.warn('hubo un error') );

    // }, [auditID]);    

    const handleInputChange = (e) => {

        // console.log(e.target.name);
        // console.log(e.target.value);
    
        setFormState({
            ...formState,
            [e.target.name]:e.target.value
        })
    
    }

    const handleCheckboxChange = (e) => {

        // console.log(e.target.name);
        // console.log(e.target.checked);

        setFormState({
            ...formState,
            [e.target.name]:e.target.checked
        })

    }

    const handleSelectChange = (e) => {
        // console.log(e.target.name);
        // console.log(e.target.value);

        setFormState({
            ...formState,
            [e.target.name]:e.target.value
        })

    }

    const handleDateChange = (date) => {

        const newDate = date ? formatDate(date) : null;

        setFormState({
            ...formState,
            veto1docsubmitdate:newDate
        })
    }

    const save = () => {

        const resp = updateIATFAudit( formState, IATFAuditUpdateTypes.PM_REVIEW_V1 );

        resp.then(saved => {
                console.log('Success:', saved);
                // if ( saved ){
                //     var element = document.getElementById("save");
                //     element.classList.add("btn-green");
                //     element.innerHTML = 'Saved !';
                // }
        })

        // console.log('formState');
        // console.log(formState);
    }

    return (
        <div className="project-manager-V1-review">
            <h5>Project Manager Review</h5>
            <br/>
            <div className="project-manager-V1-review-checkboxes">
                    
                <table className="project-manager-V1-review-table">
                    <tbody>
                        <tr>
                            <td>
                                <div className="form-switch">
                                    <input
                                        className="form-check-input" 
                                        type="checkbox" 
                                            id="veto1docsubmitted" 
                                            name="veto1docsubmitted"
                                            checked={veto1docsubmitted}
                                            onChange={handleCheckboxChange} />
                                    <label htmlFor="veto1docsubmitted">&nbsp;&nbsp;Documentation uploaded to SAP on&nbsp;&nbsp;</label>
                                </div>
                            </td>
                            <td>
                                <DatePicker 
                                    name="veto1docsubmitdate" 
                                    value={veto1docsubmitdate} 
                                    selected={startDate} 
                                    className="datepicker"                                    
                                    onChange={(date) => handleDateChange(date)} />
                            </td>
                        </tr>
                    </tbody>
                </table>

                <table className="project-manager-V1-review-veto-table">
                    <tbody>
                        <tr>
                            <td>
                                <label htmlFor="vetopersonname">Veto Person Name</label>                            
                            </td>
                            <td>                            
                                <input 
                                    type="text" 
                                    id="vetopersonname" 
                                    name="vetopersonname"
                                    className="form-control" 
                                    value={vetopersonname}
                                    onChange={handleInputChange} 
                                />
                            </td>
                        </tr>
                        <tr height="10px;"><td></td><td></td></tr>
                        <tr>
                            <td>
                                <label htmlFor="vetopersonemail">Veto Person e-mail</label>                            
                            </td>
                            <td>
                                <input 
                                    type="text" 
                                    id="vetopersonemail" 
                                    name="vetopersonemail" 
                                    className="form-control" 
                                    value={vetopersonemail}
                                    onChange={handleInputChange} />
                            </td>
                        </tr>
                        <tr height="10px;"><td></td><td></td></tr>
                        <tr>
                            <td>
                                <label htmlFor="vetopersontype">Veto Person Type</label>                            
                            </td>
                            <td>
                                <select
                                    name="vetopersontype"
                                    className="form-select"
                                    value={vetopersontype}
                                    onChange={handleSelectChange} >
                                    <option value={VetoPersonTypes.UNKNOWN}>Unknown</option>
                                    <option value={VetoPersonTypes.CRITICAL_LOCATION}>Critical Location</option>
                                    <option value={VetoPersonTypes.TO_BE_DEFINED}>To Be Defined</option>
                                    <option value={VetoPersonTypes.DEFINED_BY_GERMANY}>Defined by Germany</option>
                                </select>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
            <div className="btn-wrapper">
                <button 
                    id="save" 
                    className="btn-blue" 
                    onClick={save}>
                        SAVE
                </button>
            </div>
        </div>
    )
}

export default ProjectManagerReviewV1
