
import React, { useState } from 'react'
import FileDropZone from './FileDropZone'
import { ShowZippedFiles } from './ShowZippedFiles';
import ReportType from './globals/ReportTypes.js';

const AuditDocumentationMWF = ( ) => {

    const [zipFileName, setZipFileName] = useState("");

    return (
        
        <div className="audit-documentation-WF">
            <h5>Audit Documentation</h5>
            <br/><br/>
            <div className="audit-documentation-WF-dropzones-container">
                <FileDropZone 
                    fileType = {ReportType.MW_ZIP_FILE}
                    setZipFileName = {setZipFileName}
                />
                <ShowZippedFiles 
                    fileType = {ReportType.MW_ZIP_FILE}
                    zipFileName = {zipFileName}
                />
            </div>
        </div>

    )
}

export default AuditDocumentationMWF
