
import React, { useContext } from 'react'
import IATFAuditUpdateTypes from './globals/IATFAuditUpdateTypes.js';
import { IATFAuditContext } from './context/IATFAuditContext'
import { updateIATFAudit } from './api/updateIATFAudit.js';
import { useForm } from './hooks/useForm.js';

export const InitialStepV2 = () => {

    const audit = useContext(IATFAuditContext);

    const auditID = audit.data.id;

    const [formState, , handleInputCheck] = useForm({
        id: auditID,
        ncreviewed: audit.data.ncreviewed || false,
        clientncdocumentationsent: audit.data.clientncdocumentationsent || false
    });

    const {ncreviewed, clientncdocumentationsent} = formState;

    const save = () => {

        const resp = updateIATFAudit( formState, IATFAuditUpdateTypes.INITIAL_STEP_V2 );

        resp.then(saved => {
            console.log('Success:', saved);
            // if ( saved ){
            //     var element = document.getElementById("save");
            //     element.classList.add("btn-green");
            //     element.innerHTML = 'Saved !';
            // }
        })
    }    

    return (
        <div className="veto2-initial-step">
            <h5>Initial Step</h5>
            <br/><br/><br/>
            <div className="form-switch">
                <input
                    className="form-check-input" 
                    id="clientncdocumentationsent" 
                    type="checkbox" 
                    name="clientncdocumentationsent" 
                    checked={clientncdocumentationsent}
                    onChange={handleInputCheck} 
                />
                <label htmlFor="clientncdocumentationsent">&nbsp;&nbsp;Client sent NC documentation already</label>
            </div>
            <br/><br/><br/>
            <div className="form-switch">
                <input 
                    className="form-check-input"
                    id="ncreviewed" 
                    type="checkbox" 
                    name="ncreviewed" 
                    checked={ncreviewed} 
                    onChange={handleInputCheck}
                />
                <label htmlFor="ncreviewed">&nbsp;&nbsp;Non-conformities were reviewed</label>
            </div>
            <div className="btn-wrapper">
                <button 
                    id="save" 
                    className="btn-blue" 
                    onClick={save}>
                        SAVE
                </button>
            </div>
        </div>
    )
}
