
import React, { useContext, useState } from 'react'
import { updateIATFAudit } from './api/updateIATFAudit'
import IATFAuditUpdateTypes from './globals/IATFAuditUpdateTypes.js';
import { IATFAuditContext } from './context/IATFAuditContext'
import DatePicker from "react-datepicker";
import { formatDate } from './helpers/formatDate';
import { setStartDate } from './helpers/setStartDate';
import "react-datepicker/dist/react-datepicker.css";


export const NonConformities = ( ) => {

    const auditContext = useContext(IATFAuditContext);

    const [formState, setFormState] = useState({
        id: auditContext.data.id,
        majorncnumber: auditContext.data.majorncnumber,
        minorncnumber: auditContext.data.minorncnumber,
        specialauditmajordate: auditContext.data.specialauditmajordate ? formatDate(auditContext.data.specialauditmajordate) : null,
    });

    const {majorncnumber, minorncnumber, specialauditmajordate} = formState;

    const startDate = setStartDate(specialauditmajordate);

    const handleInputChange = (e) => {

        setFormState({
            ...formState,
            [e.target.name]:e.target.value
        })

    }

    const handleDateChange = (date) => {

        const newDate = date ? formatDate(date) : null;

        setFormState({
            ...formState,
            specialauditmajordate:newDate
        })
    }

    const save = () => {
        const resp = updateIATFAudit( formState, IATFAuditUpdateTypes.NON_CONFORMITIES );

        resp.then(saved => {
            console.log('Success:', saved);
            // if ( saved ){
            //     var element = document.getElementById("save");
            //     element.classList.add("btn-green");
            //     element.innerHTML = 'Saved !';
            // }
        })
    }

    return (
        <div className="non-conformities">
            <h5>Non-Conformities Record</h5>
            <p><br/></p>
            <div className="non-conformities-questions">
                <table>
                    <tbody>
                        <tr>
                            <td>
                                <label htmlFor="majorncnumber">Number of Major Non Conformities</label>
                            </td>
                            <td>
                                <input 
                                    type="text" 
                                    id="majorncnumber" 
                                    name="majorncnumber" 
                                    value={majorncnumber} 
                                    onChange={handleInputChange} 
                                    size="7" 
                                />
                            </td>
                        </tr>
                        <tr>
                            <td>
                                <label htmlFor="specialauditmajordate">Special Audit date</label>
                            </td>
                            <td>
                                <DatePicker 
                                    name="specialauditmajordate" 
                                    value={specialauditmajordate} 
                                    selected={startDate} 
                                    className="datepicker"                                    
                                    onChange={(date) => handleDateChange(date)} 
                                />
                            </td>
                        </tr>
                        <tr>
                            <td>
                                <label htmlFor="minorncnumber">Number of Minor Non Conformities</label>
                            </td>
                            <td>
                                <input 
                                    type="text" 
                                    id="minorncnumber" 
                                    name="minorncnumber" 
                                    value={minorncnumber} 
                                    onChange={handleInputChange} 
                                    size="7" 
                                />
                            </td>
                        </tr>
                    </tbody>
                </table>    
            </div>
            <div className="btn-wrapper">
                <button id="save" className="btn-blue" onClick={save} >SAVE</button>
            </div>
        </div>
    )
}
