
import React, { useContext, useState } from 'react'
import { updateIATFAudit } from './api/updateIATFAudit'
import IATFAuditUpdateTypes from './globals/IATFAuditUpdateTypes.js';
import { IATFAuditContext } from './context/IATFAuditContext'
import DatePicker from "react-datepicker";
import { formatDate } from './helpers/formatDate';
import { setStartDate } from './helpers/setStartDate';
import "react-datepicker/dist/react-datepicker.css";


const ProjectManagerReviewV2 = () => {

    const IATFAudit = useContext(IATFAuditContext);

    const auditID = IATFAudit.data.id;

    const [formState, setFormState] = useState({
        id: auditID,
        veto2docsubmitted: IATFAudit.data.veto2docsubmitted || false,
        veto2docsubmitdate: IATFAudit.data.veto2docsubmitdate ? formatDate(IATFAudit.data.veto2docsubmitdate) : null,
    });

    const { veto2docsubmitted, veto2docsubmitdate} = formState;

    const startDate = setStartDate(veto2docsubmitdate);

    const handleInputChange = (e) => {

        setFormState({
            ...formState,
            [e.target.name]:e.target.checked
        })

    }

    const handleDateChange = (date) => {

        const newDate = date ? formatDate(date) : null;

        setFormState({
            ...formState,
            veto2docsubmitdate:newDate
        })
    }

    const save = () => {
        const resp = updateIATFAudit( formState, IATFAuditUpdateTypes.PM_REVIEW_V2 );

        resp.then(saved => {
            console.log('Success:', saved);
            // if ( saved ){
            //     var element = document.getElementById("save");
            //     element.classList.add("btn-green");
            //     element.innerHTML = 'Saved !';
            // }
        })
    }

    return (
        <div className="project-manager-V2-review">
            <h5>Project Manager Review</h5>
            <div className="project-manager-WF-review-checkboxes">
                <table width="100%">
                    <tbody>
                        <tr>
                            <td>
                                <div className="form-switch">
                                    <input 
                                        className="form-check-input"
                                        type="checkbox" 
                                        id="veto2docsubmitted" 
                                        name="veto2docsubmitted" 
                                        checked={veto2docsubmitted} 
                                        onChange={handleInputChange}
                                    />
                                    <label htmlFor="veto2docsubmitted">&nbsp;&nbsp;Documents already uploaded to SAP on &nbsp;&nbsp;</label>
                                </div>
                            </td>
                            <td>
                                {/* <input 
                                    type="text"
                                    name="veto2docsubmitdate"
                                    size="10"
                                    value={veto2docsubmitdate}
                                    onChange={handleInputTextChange} /> */}

                                <DatePicker 
                                    name="veto2docsubmitdate" 
                                    value={veto2docsubmitdate} 
                                    selected={startDate} 
                                    className="datepicker"                                    
                                    onChange={(date) => handleDateChange(date)} 
                                />
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
            <br/>
            <div className="btn-wrapper">
                <button 
                    id="save" 
                    className="btn-blue" 
                    onClick={save}
                >
                    SAVE
                </button>
            </div>
        </div>
    )
}

export default ProjectManagerReviewV2
