
import configData from "../config/config.json";

export const uploadReport = async(IATFauditID, auditID, type, fileName, file) => {
    const formData = new FormData();

    formData.append("fileName", fileName);
    formData.append("formFile", file);

    const url = `${configData.server_url}/api/report/?IATFauditID=${IATFauditID}&auditID=${auditID}&type=${type}`;

    const resp = await fetch( url, {
        method: 'POST',
        body: formData
    });

    const response = await resp.json();

    return( response );
}

