
export const setStartDate = (vetoapproveddate) => {

    let startDate = null;
    if( vetoapproveddate ){

        const myString = vetoapproveddate + "T00:00";

        // console.log('myString');
        // console.log(myString);

        let formattedDate = new Date(myString);
        let d = '' + formattedDate.getDate();
        let m = '' + (formattedDate.getMonth() + 1); // JavaScript months are 0-11
        let y = formattedDate.getFullYear();

        if (m.length < 2) m = '0' + m;
	    if (d.length < 2) d = '0' + d;

        // console.log("d="+d);
        // console.log("m="+m);
        // console.log("y="+y);

        startDate = new Date( y + "-" + m + "-" + d + "T00:00" );
    }    

    // console.log('dentro startDate');
    // console.log(startDate);

    return ( startDate )
}
