
import React, { useContext, useEffect, useState } from 'react'
import { getZippedFileNames } from './api/getZippedFileNames';
import { saveReportDefinition } from './api/saveReportDefinition';
import { IATFAuditContext } from './context/IATFAuditContext'
import ReportTypes from './globals/ReportTypes';
import { ShowMWFileOptions } from './ShowMWFileOptions';
import { ShowV1FileOptions } from './ShowV1FileOptions';
import { ShowV2FileOptions } from './ShowV2FileOptions';


export const ShowZippedFiles = ({fileType, zipFileName}) => {

    const auditContext = useContext(IATFAuditContext);

    const IATFauditID = auditContext.data.id;

    const [zippedFiles, setZippedFiles] = useState({
        data: []
    });

    useEffect(() => {

        const resp = getZippedFileNames(IATFauditID, fileType);
        resp.then( response => {
            
            setZippedFiles({
                data: response
            });
            
        });

    }, [zipFileName]);

    
    const handleOnChange = (e) => {

        zippedFiles.data.find( z => z.id == e.target.id ).type = parseInt( e.target.value );

        setZippedFiles({
            data: zippedFiles.data,
        });

    }


    const handleSave = () => {

        const resp = saveReportDefinition(IATFauditID, fileType, zippedFiles.data);
        resp.then( response => {
            console.log('response');
            console.log(response);
        });

    }
    
    return (
         
        <div className={fileType === ReportTypes.MW_ZIP_FILE ? "zipped-files" 
                            : fileType === ReportTypes.V1_ZIP_FILE ? "zipped-files-V1" 
                            : "zipped-files-V2"
                        }>
            <table className="document-table table table-striped">
                <tbody>
                    <tr>
                        <td>Document</td>
                        <td>Type</td>
                    </tr>
                    {
                        zippedFiles.data.map( file => (
                            <tr
                                key={file.id}
                                >
                                <td
                                    key={file.id}>
                                    {file.name} 
                                </td>
                                <td>
                                    {fileType === ReportTypes.MW_ZIP_FILE ? <ShowMWFileOptions file = {file} handleOnChange = {handleOnChange} /> 
                                    : fileType === ReportTypes.V1_ZIP_FILE ? <ShowV1FileOptions file = {file} handleOnChange = {handleOnChange} />
                                    : <ShowV2FileOptions file = {file} handleOnChange = {handleOnChange} />
                                    }
                                </td>
                            </tr>
                        )) 
                    }
                </tbody>
            </table>                
        </div>

    )
}
