
import React, { useContext, useState } from 'react'
import { updateIATFAudit } from './api/updateIATFAudit'
import IATFAuditUpdateTypes from './globals/IATFAuditUpdateTypes.js';
import { IATFAuditContext } from './context/IATFAuditContext'
import DatePicker from "react-datepicker";
import { formatDate } from './helpers/formatDate';
import { setStartDate } from './helpers/setStartDate';
import "react-datepicker/dist/react-datepicker.css";


export const RequiredInformation = ( ) => {
  
    const auditContext = useContext(IATFAuditContext);

    const auditID = auditContext.data.id;

    const [formState, setFormState] = useState({
        id: auditID,
        nc100solved: auditContext.data.nc100solved || false,
        certificaterequired: auditContext.data.certificaterequired || false,
        specialauditsolveddate: auditContext.data.specialauditsolveddate ? formatDate(auditContext.data.specialauditsolveddate) : null,
    });

    const {nc100solved, certificaterequired, specialauditsolveddate} = formState;

    const startDate = setStartDate(specialauditsolveddate);

    const handleInputChange = (e) => {

        setFormState({
            ...formState,
            [e.target.name]:e.target.checked
        })

    }

    const handleDateChange = (date) => {

        const newDate = date ? formatDate(date) : null;

        setFormState({
            ...formState,
            specialauditsolveddate:newDate
        })
    }

    const save = () => {

        const resp = updateIATFAudit( formState, IATFAuditUpdateTypes.INITIAL_INFORMATION );

        resp.then(saved => {

             console.log('Success:', saved);

        })
    }

    return (
        <div className="initial-information">
            <h5>Required Information</h5>
            <br/>
            <div className="form-switch">
                <input
                    className="form-check-input" 
                    type="checkbox" 
                    id="nc100solved" 
                    name="nc100solved" 
                    checked={nc100solved} 
                    onChange={handleInputChange}
                />
                <label htmlFor="nc100solved">&nbsp;&nbsp;Non-conformities are 100% solved</label>
            </div>
            <br/>
            <table width="100%">
                <tbody>
                    <tr>
                        <td>
                            <label htmlFor="specialauditsolveddate">Special Audit date</label>
                        </td>
                        <td>

                            <DatePicker 
                                name="specialauditsolveddate" 
                                value={specialauditsolveddate} 
                                selected={startDate} 
                                className="datepicker"                                    
                                onChange={(date) => handleDateChange(date)} />
                        </td>
                    </tr>
                </tbody>
            </table>    
            <br/>
            <div className="form-switch">
                <input 
                    className="form-check-input"
                    type="checkbox" 
                    id="certificaterequired" 
                    name="certificaterequired" 
                    checked={certificaterequired} 
                    onChange={handleInputChange}/>
                <label htmlFor="certificaterequired">&nbsp;&nbsp;Certificate required</label>
            </div>
            <div className="btn-wrapper">
                <button id="save" className="btn-blue" onClick={save} >SAVE</button>
            </div>
        </div>
    )
}
