import React from 'react'
import ReportTypes from './globals/ReportTypes'

export const ShowMWFileOptions = ({file, handleOnChange}) => {

    return (
        <select 
            id={file.id}
            name={file.id}                                            
            key={file.id}
            className="form-select"
            value={file.type} 
            onChange={handleOnChange} >
                <option value={ReportTypes.UNKNOWN}>Unknown</option>
                <option value={ReportTypes.MW_QUESTIONNAIRE_OFFER}>Questionnaire for Offer Preparation</option>
                <option value={ReportTypes.MW_QUESTIONNAIRE_PLANNING}>Questionnaire for Planning of IATF 16949</option>
                <option value={ReportTypes.MW_CARA_REPORT_PRELIMINARY}>CARA Report Preliminary</option>
                <option value={ReportTypes.MW_CARA_NC_REPORT_PRELIMINARY}>CARA NC-Report</option>  
        </select>
            
    )
}
