
export const formatDateTime = (dateStr, timeSeparator = " ") => {

	const formattedDate = new Date(dateStr);
	let d = '' + formattedDate.getDate();
	let m = '' + (formattedDate.getMonth() + 1); // JavaScript months are 0-11
	const y = formattedDate.getFullYear();
    let h = '' + formattedDate.getHours();
    let mm = '' + formattedDate.getMinutes();

	if (m.length < 2) m = '0' + m;
	if (d.length < 2) d = '0' + d;
    if (h.length < 2) h = '0' + h;
    if (mm.length < 2) mm = '0' + mm;

	//return (m + "/" + d + "/" + y + " " + h + ":" + mm);
	return (y + "-" + m + "-" + d + timeSeparator + h + ":" + mm);
	
}

