
import configData from "../config/config.json";

export const getReport = async(IATFauditID, type) => {

    const url = `${configData.server_url}/api/report/${IATFauditID},${type}`;

    const resp = await fetch(url);

    const data = await resp.json();

    return {
        id: data.id,
        name: data.name,
        uploadDate: data.uploadDate,
        type: data.type,
    };

}

