
import React, { useContext, useState } from 'react'
import FileDropZone from './FileDropZone'
import ReportType from './globals/ReportTypes.js';
import { IATFAuditContext } from './context/IATFAuditContext'
import DatePicker from "react-datepicker";
import { formatDate } from './helpers/formatDate';
import { updateIATFAudit } from './api/updateIATFAudit';
import IATFAuditUpdateTypes from './globals/IATFAuditUpdateTypes';
import { setStartDate } from './helpers/setStartDate';
import "react-datepicker/dist/react-datepicker.css";


export const Approval = () => {

    const audit = useContext(IATFAuditContext);

    const auditID = audit.data.id;

    const [formState, setFormState] = useState({
        id: auditID,
        vetoapproved: audit.data.vetoapproved || false,
        vetoapproveddate: audit.data.vetoapproveddate ? formatDate(audit.data.vetoapproveddate) : null,
    });

    const {vetoapproved, vetoapproveddate} = formState;

    const startDate = setStartDate(vetoapproveddate);
    
    const [setZipFileName] = useState("");

    const handleInputChange = (e) => {
        
        setFormState({
            ...formState,
            [e.target.name]:e.target.checked
        })

    } 
    
    const handleDateChange = (date) => {
        
        const newDate = date ? formatDate(date) : null;

        setFormState({
            ...formState,
            vetoapproveddate:newDate
        })

    }    

    const save = () => {
        const resp = updateIATFAudit( formState, IATFAuditUpdateTypes.APPROVAL );

        resp.then(saved => {
            console.log('Success:', saved);
            // if ( saved ){
            //     var element = document.getElementById("save");
            //     element.classList.add("btn-green");
            //     element.innerHTML = 'Saved !';
            // }
        })
    }

    return (
        <div className="approval">
            <h5>Approval</h5>
            <br/><br/>
            <div className="approval-container">
                <FileDropZone 
                    fileType = {ReportType.APPROVAL_EMAIL}
                    setZipFileName = {setZipFileName}
                />                
                <div className="approval-questions">
                    <table width="100%">
                        <tbody>
                            <tr>
                                <td>
                                    <div className="form-switch">
                                        <input
                                            className="form-check-input" 
                                            type="checkbox" 
                                            id="vetoapproved"
                                            name="vetoapproved" 
                                            checked={vetoapproved}
                                            onChange={handleInputChange} 
                                        />
                                        <label htmlFor="vetoapproved">&nbsp;&nbsp;Documentation approved by Veto on &nbsp;&nbsp;</label>
                                    </div>
                                </td>
                                <td>
                                    <DatePicker 
                                        name="vetoapproveddate" 
                                        value={vetoapproveddate} 
                                        selected={startDate} 
                                        className="datepicker"                                    
                                        onChange={(date) => handleDateChange(date)} 
                                    />
                                </td>
                            </tr>
                            
                        </tbody>
                    </table>
                </div>                
                <div className="btn-wrapper">
                    <button 
                        id="save" 
                        className="btn-blue" 
                        onClick={save} >
                            SAVE
                    </button>
                </div>
            </div>
        </div>
    )
}
