import React, { useEffect, useState } from 'react'
import ProjectManagerReviewMWF from './ProjectManagerReviewMWF'
import AuditDocumentationMWF from './AuditDocumentationMWF'
import Header from './Header'
import Log from './Log'
import { NonConformities } from './NonConformities'
import Notes from './Notes'
import Status from './Status'
import AuditDocumentationV1 from './AuditDocumentationV1'
import ProjectManagerReviewV1 from './ProjectManagerReviewV1'
import { DownloadAuditTemplates } from './DownloadAuditTemplates'
import { AuditContext } from './context/AuditContext'
import { IATFAuditContext } from './context/IATFAuditContext'
import { getAudit } from './api/getAudit'
import { getIATFAudit } from './api/getIATFAudit'
import { RequiredInformation } from './RequiredInformation'
import { AuditDocumentationV2 } from './AuditDocumentationV2'
import ProjectManagerReviewV2 from './ProjectManagerReviewV2'
import { Approval } from './Approval'
import { InitialStepV2 } from './InitialStepV2'
import { AuditInformation } from './AuditInformation'
import { VetoRejection } from './VetoRejection'


export const MainApp = ({IATFauditID}) => {

    const [IATFaudit, setIATFaudit] = useState();

    const [audit, setAudit] = useState();

    useEffect(() => {

        getIATFAudit(IATFauditID)
        .then( audit => {
          
            setIATFaudit({
                data: audit
            })

            getAudit(audit.auditid)
            .then( audit => {

                setAudit({
                    data: audit
                })

            });

        });

    }, [IATFauditID]);

    const [logState, setLogState] = useState()

    


    return (
        <>

            <div className="main-container">    

     

                <div className="initial-information-label">
                    First Steps
                </div>

                <div className="miniworkflow-label">
                    Mini Workflow Process
                </div>

                <div className="veto1-label">
                    Veto 1 Process
                </div>

                <div className="veto2-label">
                    Veto 2 Process
                </div>

                <div className="approval-label">
                    Approval Process
                </div>

                <AuditContext.Provider value={audit}>
                    { audit && <Header /> } 
                    { audit && <AuditInformation /> }
                    { audit && <DownloadAuditTemplates /> }
                </AuditContext.Provider>

                <IATFAuditContext.Provider value={IATFaudit}>
                    { IATFaudit && <Status /> }
                    { IATFaudit && <Log /> }
                    { IATFaudit && <Notes /> }

                    { IATFaudit && <RequiredInformation /> }
                    { IATFaudit && <AuditDocumentationMWF /> }
                    { IATFaudit && <ProjectManagerReviewMWF /> }
                    { IATFaudit && <NonConformities /> }
                    { IATFaudit && <ProjectManagerReviewV1 /> }

                    { IATFaudit && <AuditDocumentationV1 /> }
                    
                    { IATFaudit && <InitialStepV2 /> }
                    { IATFaudit && <AuditDocumentationV2 /> }
                    { IATFaudit && <ProjectManagerReviewV2 /> }
                    { IATFaudit && <VetoRejection /> }

                    { IATFaudit && <Approval /> }
                </IATFAuditContext.Provider>

            </div>  
        </>
    )
}

export default MainApp
