import React from 'react'
import ReportTypes from './globals/ReportTypes'

export const ShowV1FileOptions = ({file, handleOnChange}) => {
    return (
        <select 
            id={file.id}
            name={file.id}                                            
            key={file.id}
            className="form-select"
            value={file.type} 
            onChange={handleOnChange} >
                <option value={ReportTypes.UNKNOWN}>Unknown</option>
                <option value={ReportTypes.V1_APPLICATION_EXTENDED}>Application Extended Manufacturing Site</option>
                <option value={ReportTypes.V1_READINESS_PRE_CHECK}>Readiness Pre-Check</option>
                <option value={ReportTypes.V1_STAGE1_AUDIT_PLAN}>Stage 1 Audit Plan</option>
                <option value={ReportTypes.V1_QM_SYSTEM}>QM-System Doc-Review Input and Evaluation</option>  
                <option value={ReportTypes.V1_PROCESS_OVERVIEW}>Process Overview and Performance Information</option>  
                <option value={ReportTypes.V1_STAGE1_AUDIT_REPORT}>Stage 1 Audit Report</option>  
                <option value={ReportTypes.V1_AUDIT_PLAN}>Audit Plan</option>  
                <option value={ReportTypes.V1_CARA_RL_REPORT}>CARA RL-Report</option>  
                <option value={ReportTypes.V1_SCORECARDS}>Scorecards</option>  
                <option value={ReportTypes.V1_COVER_SHEET}>Cover Sheet for as well as Handwritten Notes</option>  
                <option value={ReportTypes.V1_HANDWRITTEN_NOTES}>Handwritten Notes</option>  
                <option value={ReportTypes.V1_RELEASE_PROTOCOL}>VETO-1 Release Protocol</option>  
                <option value={ReportTypes.V1_CERTIFICATE_ORDER}>Certificate order form</option>  
                <option value={ReportTypes.V1_AUDIT_PLAN_RELEASE}>Audit Plan and Release Protocol in attachment to CARA print/file</option>  
                <option value={ReportTypes.V1_REQUEST_TRANSFER_AUDIT}>Request for Transfer Audit</option>  
                <option value={ReportTypes.V1_PREVIOUS_CB_REPORTS}>Previous CB Reports</option>  
                <option value={ReportTypes.V1_PREVIOUS_CB_EVIDENCE}>Previous CB Evidence</option>  
                <option value={ReportTypes.V1_PREVIOUS_CB_CERTIFICATE}>Previous CB Certificate</option>  
                <option value={ReportTypes.V1_IATF_REMOTE}>IATF Remote Audit-Application</option>  
                <option value={ReportTypes.V1_OTHER_DOCUMENTS}>Other Documents/files/records</option>  
        </select>
    )
}

