
export const formatDate = (dateStr) => {

	let formattedDate = new Date(dateStr);
	let d = '' + formattedDate.getDate();
	let m = '' + (formattedDate.getMonth() + 1); // JavaScript months are 0-11
	let y = formattedDate.getFullYear();

	if (d.length < 2) d = '0' + d;
	if (m.length < 2) m = '0' + m;

	//return (m + "/" + d + "/" + y);
	return (y + "-" + m + "-" + d);
	
}

