
import React, { useContext, useState } from 'react'
import { updateIATFAudit } from './api/updateIATFAudit';
import { IATFAuditContext } from './context/IATFAuditContext'
import IATFAuditUpdateTypes from './globals/IATFAuditUpdateTypes.js';
import { IATFAuditStatus } from './globals/IATFAuditStatus';

const Status = ( ) => {

    const auditContext = useContext(IATFAuditContext);

    const [audit, setAudit] = useState({
        id: auditContext.data.id,
        status: auditContext.data.status,
        statusdescription: auditContext.data.statusdescription
    })

    const getBackgroundColor = () => {

        let backgroundColor = '#c6efce';
        
        if(audit.status === IATFAuditStatus.REJECTED){
            backgroundColor = '#ffc7ce';
        }

        return backgroundColor;
       
    }

    const handleClick = () => {

        let statusDescription = '';
        let statusNumber = 0;

        if(audit.status === IATFAuditStatus.SCHEDULED){
            statusDescription = 'In Veto Process';
            statusNumber = IATFAuditStatus.IN_VETO_PROCESS;
        } else if(audit.status === IATFAuditStatus.IN_VETO_PROCESS){
            statusDescription = 'Rejected';
            statusNumber = IATFAuditStatus.REJECTED;
        } else if(audit.status === IATFAuditStatus.REJECTED){
            statusDescription = 'Completed';
            statusNumber = IATFAuditStatus.COMPLETED;
        } else if(audit.status === IATFAuditStatus.COMPLETED){
            statusDescription = 'Scheduled';
            statusNumber = IATFAuditStatus.SCHEDULED;
        }

        setAudit({
            id: audit.id,
            status: statusNumber,
            statusdescription: statusDescription
        });        

    }

    console.log(audit)

    const save = () => {

        const IATFaudit = {
            id: audit.id,
            status: audit.status
        }

        const resp = updateIATFAudit( IATFaudit, IATFAuditUpdateTypes.STATUS );

        resp.then(saved => {
            
            if ( saved ){
                console.log('Success:', saved);

                // var element = document.getElementById("save");
                // element.classList.add("btn-green");
                // element.innerHTML = 'Saved !';
            }

        })

    }

    return (
        <div 
            className="status" 
            id="status"
            style={{backgroundColor: getBackgroundColor()}}
        >
            <h5>Audit Status</h5>
            <div className="status-text">
                <p 
                    id="status-text-label" 
                    onClick={handleClick}>
                        {audit.statusdescription}
                </p>
            </div>
            <div className="btn-wrapper">
                <button 
                    id="save" 
                    className="btn-blue"
                    onClick={save} >
                        UPDATE
                </button>
            </div>
        </div>
    )
}
 
export default Status
