
import React, { useContext, useState } from 'react'
import { updateIATFAudit } from './api/updateIATFAudit'
import IATFAuditUpdateTypes from './globals/IATFAuditUpdateTypes.js';
import { IATFAuditContext } from './context/IATFAuditContext'
import DatePicker from "react-datepicker";
import { formatDate } from './helpers/formatDate';
import { setStartDate } from './helpers/setStartDate';
import "react-datepicker/dist/react-datepicker.css";


const ProjectManagerReviewMWF = () => {

    const audit = useContext(IATFAuditContext);

    const auditID = audit.data.id;

    const [formState, setFormState] = useState({
        id: auditID,
        docsubmitsap: audit.data.docsubmitsap || false,
        docsubmitdate: audit.data.docsubmitdate ? formatDate(audit.data.docsubmitdate) : null,
    });

    const {docsubmitsap, docsubmitdate} = formState;

    const startDate = setStartDate(docsubmitdate);

    // useEffect( () => {
        
    //     getIATFAudit(auditID)
    //     .then( audit => {
    //         setFormState({
    //             id: audit.id,
    //             ncreviewed: audit.ncreviewed,
    //             nc100solved: audit.nc100solved,
    //             certificaterequired: audit.certificaterequired,
    //             docsubmitsap: audit.docsubmitsap
    //         })
    //     });
    //     // .catch( console.warn('hubo un error') );

    // }, [auditID]);    

    const handleInputChange = (e) => {

        setFormState({
            ...formState,
            [e.target.name]:e.target.checked
        })

    }

    // const handleInputTextChange = (e) => {

    //     setFormState({
    //         ...formState,
    //         [e.target.name]:e.target.value
    //     })

    // }

    const handleDateChange = (date) => {

        const newDate = date ? formatDate(date) : null;

        setFormState({
            ...formState,
            docsubmitdate:newDate
        })
    }

    const save = () => {
        const resp = updateIATFAudit( formState, IATFAuditUpdateTypes.PM_REVIEW_MW );

        resp.then(saved => {
            console.log('Success:', saved);
            // if ( saved ){
            //     var element = document.getElementById("save");
            //     element.classList.add("btn-green");
            //     element.innerHTML = 'Saved !';
            // }
        })
    }

    return (
        <div className="project-manager-WF-review">
            <h5>Project Manager Review</h5>
            <div className="project-manager-WF-review-checkboxes">
                <table>
                    <tbody>
                        <tr>
                            <td>
                                <div className="form-switch">
                                    <input 
                                        className="form-check-input"
                                        type="checkbox" 
                                        id="docsubmitsap" 
                                        name="docsubmitsap" 
                                        checked={docsubmitsap} 
                                        onChange={handleInputChange}
                                    />
                                    <label htmlFor="docsubmitsap">&nbsp;&nbsp;Audit documents already uploaded to SAP on &nbsp;&nbsp;</label>
                                </div>
                            </td>
                            <td>
                                <DatePicker 
                                    name="docsubmitdate" 
                                    value={docsubmitdate} 
                                    selected={startDate} 
                                    className="datepicker"                                    
                                    onChange={(date) => handleDateChange(date)} />
                            </td>
                        </tr>
                    </tbody>
                </table>                                
            </div>
            <br/>
            <div className="btn-wrapper">
                <button 
                    id="save" 
                    className="btn-blue" 
                    onClick={save}>
                        SAVE
                </button>
            </div>
        </div>
    )
}

export default ProjectManagerReviewMWF
