
import React, { useCallback, useContext, useEffect, useState } from 'react';
import {useDropzone} from 'react-dropzone';
import downloadImage from '../assets/download-cloud-icon-15.png'
import deleteImage from '../assets/icon-delete-19.png'
import { uploadReport } from './api/uploadReport'
import { getReport } from './api/getReport';
import { downloadReport } from './api/downloadReport';
import { deleteReport } from './api/deleteReport';
import ReportType from './globals/ReportTypes.js';
import { IATFAuditContext } from './context/IATFAuditContext'
import { getCurrentDate } from './helpers/getCurrentDate';
import { getDropZoneMessage } from './helpers/getDropZoneMessage';
import { formatDate } from './helpers/formatDate';


function FileDropZone({fileType, setZipFileName}) {

	const dropZoneMessage = getDropZoneMessage(fileType);

	const auditContext = useContext(IATFAuditContext);

    const IATFauditID = auditContext.data.id;
    const auditID = auditContext.data.auditid;

	const [report, setReport] = useState({
		reportID: 0,
		name: '',
		uploadDate: '',
		type: 0
	})	

	const {reportID, name, uploadDate} = report;	
     
    const onDrop = useCallback((acceptedFiles) => {

		acceptedFiles.forEach((file) => {

			const resp = uploadReport(IATFauditID, auditID, fileType, file.path, file);
			resp.then( response =>{             
				
				setReport({
					reportID: response,
					name: file.path,
					uploadDate: formatDate(getCurrentDate()),
					type: fileType
				});

			})
			.catch( error =>{
				console.log(error);
			});
		
		});
      
    }, [])


	useEffect( () => {

		// if(name !== ''){
		// 	return;
		// }

		const resp = getReport(IATFauditID, fileType);
		resp.then( report => {

			setReport({
				reportID: report.id,
				name: report.name,
				uploadDate: report.uploadDate && formatDate(report.uploadDate),
				type: report.type
			})

			// type === ReportType.AUDIT_ZIP_FILE && name != null && setZipFileName(name);

			if( name != null && (fileType === ReportType.MW_ZIP_FILE || fileType === ReportType.V1_ZIP_FILE || fileType === ReportType.V2_ZIP_FILE) ) {
				setZipFileName(name);
			}

		})
		// .catch( error =>{
		//   console.log("error=" + error);
		// })

	}, [name]);

	const downloadFile = () => {

		if (!reportID)
			return;

		const resp = downloadReport( auditID, reportID );
		resp.then( response => {

            const file = new Blob(
                [response.data],
                {type: 'application/octet-stream'}
            );

            const fileURL = URL.createObjectURL(file);
            const a = document.getElementById('a_id');
            const filename = name;
            a.href = fileURL;
            a.download = filename;
            a.click();
            window.URL.revokeObjectURL(fileURL);

        });
		
	}

	const deleteFile = () => {
				
		if (!reportID)
		return;
		
		const resp = deleteReport( auditID, reportID );
		resp.then( response => {

			setReport({
				reportID: 0,
				name: '',
				uploadDate: '',
				type: 0
			});
			
		});
		
	}

	let fileExtension = "";
	if( fileType === ReportType.MW_ZIP_FILE || fileType === ReportType.V1_ZIP_FILE || fileType === ReportType.V2_ZIP_FILE ) {
		fileExtension = ".zip"
	}
	const {fileRejections, getRootProps, getInputProps} = useDropzone({onDrop, maxFiles: 1, accept: fileExtension});
	const fileRejectionsItems = fileRejections.map ( ({ file, errors }) => (
		console.log('entre al reject, rechac√© al ' + file.name)
	));

    const displayDropZoneMessage = (
		<p className="file-dropzone-message">Drag 'n' drop <b>{dropZoneMessage}</b> here or click to select files</p>
	);	

	const displayUploadedFileDetails = (
		<>        
			<input title={name} value={name} className="file-dropzone-filename-input" disabled />
			<input value={uploadDate} className="file-dropzone-filename-input" disabled />
		</>
	);


    return (

      <div className="file-dropzone-container">
        <div {...getRootProps({className: 'dropzone'})}>
        	<input {...getInputProps()}  />
        		{displayDropZoneMessage}
        </div>
        <div className="file-dropzone-button-container">
        	<button className="file-dropzone-button" onClick={downloadFile}><img src={downloadImage} className="file-dropzone-button-image" alt=""/></button>          
        	<button className="file-dropzone-button" onClick={deleteFile}><img src={deleteImage} className="file-dropzone-button-image" alt=""/></button>          
        </div>
        <div>
			{ displayUploadedFileDetails }
        </div>		
      </div>

    );
	
  }

  export default FileDropZone;