
import React, { useContext, useEffect, useState } from 'react'
import { getLastNote } from './api/getLastNote';
import { IATFAuditContext } from './context/IATFAuditContext'
import { AddNotes } from './AddNotes';
import { getCurrentDate } from './helpers/getCurrentDate';
import { formatDateTime } from './helpers/formatDateTime';

const Notes = ( ) => {

    const auditContext = useContext(IATFAuditContext);

    const auditID = auditContext.data.id;

    const [note, setNote] = useState({
        id: 0,
        auditid: 0,
        notes: "",
        creationDate: getCurrentDate(),
    });

    const {id} = note;

    useEffect(() => {
        
        getLastNote(auditID)
        .then( note => {
            setNote({
                id: note.id,
                auditid: note.auditid,
                notes: note.notes,
                creationDate: formatDateTime(note.creationDate),
            })
        });
        // .catch( console.warn('hubo un error') );
    }, [id, auditID])

    const [show, setShow] = useState(false);
    
    return (
        <>

            <AddNotes 
                show = {show}
                onHide = {() => setShow(false)}
                setNote = {setNote}
            />

            <div className="notes">
                <h5>Audit Notes</h5>
                <div className="note-text">                    
                    {note.notes && `Last note created on ${formatDateTime( note.creationDate )} :` }
                    <br/><br/>
                    <p>{note.notes}</p>
                </div>
                <div className="btn-wrapper">
                    <button 
                        className="btn-blue"
                        variant="primary"
                        onClick={() => setShow(true)}
                    >
                        MORE
                    </button>
                </div>

            </div>

        </>
    )
}

export default Notes
