
import configData from "../config/config.json";

export const getIATFAudit = async( id ) => {

    const url = `${configData.server_url}/api/IATFAudit/${id}`;

    const resp = await fetch(url);

    const data = await resp.json();  

    return {
        id: data.id,
        auditid: data.auditID,
        status: data.status,
        statusdescription: data.statusDescription,
        ncreviewed: data.ncReviewed,
        nc100solved: data.nC100Solved,
        certificaterequired: data.certificateRequired,
        docsubmitsap: data.docSubmitSAP,
        docsubmitdate: data.docSubmitDate,
        majorncnumber: data.majorNCNumber,
        minorncnumber: data.minorNCNumber,
        specialauditsolveddate: data.specialAuditSolvedDate,
        specialauditmajordate: data.specialAuditMajorDate,
        veto1docsubmitted: data.veto1DocSubmitted,
        veto1docsubmitdate: data.veto1DocSubmitDate, 
        vetopersonname: data.vetoPersonName, 
        vetopersonemail: data.vetoPersonEmail, 
        vetopersontype: data.vetoPersonType,
        veto2docsubmitted: data.veto2DocSubmitted,
        veto2docsubmitdate: data.veto2DocSubmitDate, 
        clientncdocumentationsent: data.clientNCDocumentationSent,
        vetoapproved: data.vetoApproved,
        vetoapproveddate: data.vetoApprovedDate,
        vetorejected: data.vetoRejected,
        vetorejecteddate: data.vetoRejectedDate,
        vetofeedback: data.vetoFeedback,
    };
}

