
import React, { useContext, useEffect, useState } from 'react'
import { getLog } from './api/getLog'
import { IATFAuditContext } from './context/IATFAuditContext'
import { formatDateTime } from './helpers/formatDateTime';


const Log = () => {

    const auditContext = useContext(IATFAuditContext);
    const auditID = auditContext.data.id;

    const [auditList, setAuditList] = useState();


    useEffect(() => {
        
        getLog( auditID )
        .then( response => {
            setAuditList(response);
        });
    
    }, [auditID])
    

    return (
        <div className="log">
            <h5>Audit Log</h5>
            <div className="log-list">
                <table className="table">
                    <tbody>
                        {
                            auditList && auditList.data.map( audit => (
                                <tr key={audit.id}>
                                    <td key={audit.id} height="30px">
                                        {audit.description} on {formatDateTime(audit.creationDate) }
                                    </td>
                                </tr>
                            ))
                        }
                    </tbody>
                </table>
            </div>
        </div>
    )

}

export default Log
