
import React, { useContext } from 'react'
import downloadImage from '../assets/download-cloud-icon-15.png'
import { downloadTemplates } from './api/downloadTemplates'
import { AuditContext } from './context/AuditContext'


export const Download = ( ) => {

    const auditContext = useContext(AuditContext);

    const auditID = auditContext.data.id;

    const getStandardTemplates = () => {
        
        const resp = downloadTemplates(auditID);
        resp.then( response => {

            const file = new Blob(
                [response.data],
                {type: 'application/octet-stream'}
            );

            const fileURL = URL.createObjectURL(file);
            const a = document.getElementById('a_id');
            const filename = 'IATF_templates.zip';
            a.href = fileURL;
            a.download = filename;
            a.click();
            window.URL.revokeObjectURL(fileURL);

        });

    }

    return (
        <div className="download-button-container">
            <button 
                className="download-button" 
                onClick={getStandardTemplates}>
                <img 
                    src={downloadImage} 
                    className="download-button-image" 
                    alt="" />
            </button>
            <br/>
            Click to download templates
            <a id="a_id" href="." hidden>this anchor is a helper to download standard template files</a>
        </div>
    )
}

